import React, { useState, useEffect } from 'react'
import SavedExpandableTile from './SavedExpandableTile'
import NavigationBar from '../../shared/components/NavigationBar'
import axios from 'axios'
import history from '../../History'
import userStore from '../../redux/userStore'
import {
  SavedPageContainer,
  ButtonContainer,
  FilterButton,
  SavedContainer,
  LoadingContainer,
  Loading
} from './Styles'

const Saved = () => {
  const [saved, setSaved] = useState([])
  let path = history.location.pathname

  useEffect(() => {
    if(path === '/saved') {
      const userId ={
        user_id: `${userStore.getState().id ? userStore.getState().id : 1}`
      }

      axios({
        method: 'post',
        url: '/pairs/getSavedPairs',
        baseURL: '/',
        data: userId
      })
        .then(response => {
          setSaved(response.data)
        })
        .catch(error => {
          console.log(error)
        })
    } else if(path === '/popular') {
      console.log('popular')
      axios({
        method: 'get',
        url: '/pairs/getPopularPairings',
        baseURL: '/'
      })
        .then(response => {
          setSaved(response.data)
        })
        .catch(error => {
          console.log(error)
        })
    }
    return setSaved([])
  }, [path])

  const displaySaved = () => saved.map(pair => <SavedExpandableTile pair={pair} key={pair.node_id} />)

  if(saved.length === 0){
    return (
      <LoadingContainer>
        <NavigationBar />
        <Loading />
      </LoadingContainer>
    )
  } else {
    return (
      <SavedPageContainer>
        <NavigationBar />
        <ButtonContainer>
          <FilterButton>Filter</FilterButton>
        </ButtonContainer>
        <SavedContainer>
          {displaySaved()}
        </SavedContainer>
      </SavedPageContainer>
    )
  }
  
}

export default Saved
