import React, { useState } from "react";
import Wine from "./wine-pre.js";
import Cheese from "./cheese-pre.js";
import Password from "./password.js";
import UserInfo from "./user-info.js";
import Picture from "./picturePro.js";

import userStore from '../../redux/userStore' // redux import

import NavigationBar from '../../shared/components/NavigationBar'

import { makeStyles } from "@material-ui/core/styles";
import {
  Paper,
  Typography,
  Container,
  ExpansionPanelSummary,
  ExpansionPanel,
  ExpansionPanelDetails,
  Grid
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

//import { Redirect } from "react-router-dom";
import { Helmet } from "react-helmet";
const useStyles = makeStyles(theme => ({
  root: {
    top:"15%",
    width: "98%",
    position: "relative",
    flexGrow: 1,
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular
  },
  paper: {
    backgroundColor : '#fff8e1', 
  },
  title: {
    position: "absolute",
    left: "52%",
    top: "24%",
  
    transform: "translate(-50%, -50%)",
    fontSize: 22,
    flexGrow: 1
  },
  mainDiv: {
    marginLeft: '5px',
    marginRight: '5px',
    marginBottom: '100px'
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(8)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
}));

const Profile = () => {

  const [user, setUser] = useState(userStore.getState());

  const [oneTime, setOneTime] = React.useState(true);
  
if(oneTime){
if(user.first_name !== undefined){
sessionStorage.setItem("firstName",user.first_name)
}
setOneTime(false)
}

  const classes = useStyles();
  return (
    <Paper className={classes.paper}>
      <NavigationBar></NavigationBar>
      <Helmet bodyAttributes={{ style: "background-color : #fff8e1" }} />
      <Typography className={classes.title}>{sessionStorage.getItem("firstName")}</Typography>
      
      <div className={classes.mainDiv}>
     
          <Picture />
          <Grid></Grid>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >

              <Typography className={classes.heading}>
                Personal Information
              </Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <UserInfo />
            </ExpansionPanelDetails>
          </ExpansionPanel>

          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography className={classes.heading}> Password</Typography>
            </ExpansionPanelSummary>
            
            <ExpansionPanelDetails>
              <Password />
            </ExpansionPanelDetails>
          </ExpansionPanel>

          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2a-content"
              id="panel2a-header"
            >
              <Typography className={classes.heading}> Preferences</Typography>
            </ExpansionPanelSummary>
          
          <ExpansionPanelDetails>
            <Cheese />
          </ExpansionPanelDetails>
          </ExpansionPanel>

      </div>
    </Paper>
  );
};

export default Profile;
