import React, { useState } from 'react'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import ExpandLessIcon from '@material-ui/icons/ExpandLess'
import {
  TileContainer,
  CompressedTile,
  ExpandedTile,
  Heading,
  HorizontalLine,
  WineDescription
} from './Styles'

const SavedExpandableTile = ({ pair }) => {
  console.log(pair)
  const [expanded, setExpanded] = useState(false)
  if (expanded) {
    return (
      <TileContainer>
        <CompressedTile onClick={() => setExpanded(!expanded)}>
          {pair.wine + ' + ' + pair.cheese}
          <ExpandLessIcon />
        </CompressedTile>
        <ExpandedTile>
          <Heading>{pair.wine}</Heading>
          <WineDescription>{pair.wine_list[0].description}</WineDescription>
          <HorizontalLine />
          <Heading>{pair.cheese}</Heading>
          {pair.country + ' | ' + pair.texture + ' | ' + pair.animal}<br />
        </ExpandedTile>
      </TileContainer>
    )
  } else {
    return (
      <CompressedTile onClick={() => setExpanded(!expanded)}>
        {pair.wine + ' + ' + pair.cheese}
        <ExpandMoreIcon />
      </CompressedTile>
    )
  }
}

export default SavedExpandableTile
